const Table = ({ users }) => (
  <table>
    <thead>
      <tr>
        <th>Username</th>
        <th>Age</th>
      </tr>
    </thead>
    <tbody>
      {users.map((user, index) => (
        <tr key={index}>
          <td>{user.username}</td>
          <td>{user.age}</td>
        </tr>
      ))}
    </tbody>
  </table>
);

export default Table;
